:root {
  --cell-size: 3vw;
  --snake-head-color: red;
  --snake-body-color: green;
  --empty-color: orange;
}

@media (min-aspect-ratio: 1/1) {
  :root {
    --cell-size: 3vh;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}



